import Vue from 'vue';
import Router from 'vue-router';
import stub from './views/stub.vue';
import routePage from './views/route-page.vue';
import viewPage from './views/view.vue';
import product from './views/product.vue';

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'route-page',
      component: routePage
		},
		{
      path: '/stub',
      name: 'stub',
      component: stub
		},
		{
      path: '/bogels',
      name: 'bogels',
      component: viewPage,
		},
		{
      path: '/desserts',
      name: 'desserts',
      component: viewPage,
		},
		{
      path: '/drinks',
      name: 'drinks',
      component: viewPage,
		},
		{
      path: '/sales',
      name: 'sales',
      component: viewPage,
		},
		
		{
      path: '/product',
      name: 'product',
      component: product
    },
  ]
})
