<template>
	<div class="wrapper" id="app" :class='{ "on-product" : onProductPage}'>
		<headerElement :lang='lang' :base='store'></headerElement>
		
		<router-view :lang='lang' :base='store'></router-view>
	</div>
</template>

<script>
	import headerElement from '@/components/header';

	export default {
		name: 'router-page',
		components: {
			headerElement
		},
		data() {
			return {
				onProductPage: false,
				pathName: window.location.pathname.split('/')[1]
			}
		},
		created() {
			this.$router.push('/');
			this.getData();
			this.pathName === 'product' ? this.onProductPage = true : this.onProductPage = false;
			this.setCurrentCategory(this.pathName);
		},
		methods: {
			getData() {
				this.$store.commit('loadData');
			},
			setCurrentCategory(category) {
				this.$store.commit('setCurrentCategory', category);
			},
		},
		computed: {
			store() {
				return this.$store;
			},
			lang() {
				let lang = this.$store.state.pageLang;
				return lang.toLowerCase();
			}
		},
		watch: {
			$route(to, from) {
				const [category, fromPage] = [ to.name, from.name ];
				
				category === 'product' ? this.onProductPage = true : this.onProductPage = false;
				if(!this.onProductPage && category !== 'route-page') this.setCurrentCategory(category);
			}
		}
	}
</script>


