<template>
	<main class="content product-page">

		<button @click='stepBack' class="page-back"></button>

		<div class="page-title">
			<img src="../assets/images/bogel-text-logo.svg" alt="text logo">
			<h1>{{product.title[lang]}}</h1>
		</div>

		<div class="page-content product">

			<div class="product__portions">

				<div v-for='(portion, index) in product["portion-price"]' :key=index class="product__portion-info">

					<p v-if='portion.type' class="product__type">{{portion.type[lang]}}</p>

					<div class="label product__label">
						<p>{{portion.price + ' &#8372;'}}</p>
					</div>

					<div class="label is-grey product__label">
						<p>{{portion.weight}} {{staticText.gramms[lang]}}</p>
					</div>

				</div>

			</div>

			<div class="product__content">

				<div class="product__block">

					<p class="product__descr-name">
						{{staticText.descr[lang]}}:
					</p>

					<p class="product__descr">
						{{product.descr[lang]}}
					</p>

					<button :class='{ "is-hidden" : scrollBarPos.start}' @click='barScroled' class="product__ingredients__more to-top"></button>
					
					<vue-custom-scrollbar ref="scrollBar" @ps-scroll-y="checkBarPos" :class='{ "full-window" : window.height > 1000, "not-full-window" : window.height < 1000}' class="product__ingredients__wrapper">
						<div class="product__ingredients">

							<div v-for='(ingredient, index) in product.ingredients' :key='index' class="product__ingredient">
								<img :src='ingredient.logo' alt="">
								<p>{{ingredient.name[lang]}}</p>
							</div>

							<div v-for='(subProduct, index) in product["consist-products-id"]' :key='index' class="product__ingredient">
								<img :src='catImages[subProduct.category]' alt="category">
								<p>{{ dataBase.getters.getAllProducts[subProduct.category].find( (element, index) => index === subProduct.id ).title[lang] }}</p>
								<button class="product__button" @click='openProduct(subProduct)'></button>
							</div>

						</div>
					</vue-custom-scrollbar>

					<button :class='{ "is-hidden" : scrollBarPos.end}' @click='barScroled("bottom")' class="product__ingredients__more"></button>

				</div>

				<div class="product__block">

					<swiper class="product__slider" :options="swiperOption" ref="productSwiper">
						<swiper-slide v-for='(slide, index) in product.gallery' :key='index' class="product__slide">
							<img :src='slide' :alt='"product" + index'>
						</swiper-slide>
					</swiper>

					<div class="product__bg-decor"></div>

					<div class="page-content__nav">
						<button @click='slideLeft' class="page-content__arrow">
							<span></span>
						</button>
						<button @click='slideRight' class="page-content__arrow">
							<span></span>
						</button>
					</div>

				</div>

			</div>

		</div>

	</main>
</template>


<script>
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import vueCustomScrollbar from 'vue-custom-scrollbar';

export default {
	name: 'productPage',
	props: ['base', 'lang'],
	components: {
		swiper,
		swiperSlide,
		vueCustomScrollbar
	},
	data() {
		return {
			staticText: {
				gramms: {
					'ru': 'г',
					'ua': 'г',
					'en': 'g'
				},
				descr: {
					'ru': 'Ингридиенты / Описание',
					'ua': 'Інгредієнти / Опис',
					'en': 'Ingridients / description'
				}
			},
			window:{
				height: window.innerHeight
			},
			scrollBarPos: {
				start: true,
				end: false,
			},
			swiperOption: {
				slidesPerView: 1,
				slidesPerGroup: 1,
			},
			product: null,
			dataBase: this.base,
			catImages: './images/sales-cat-1.svg',
			cacheProduct: null,
			subProducts: null
		}
	},
	created() {
		this.loadProduct();
	},
	mounted() {
		this.catImages = this.dataBase.getters.getAllProducts['category-images'];
	},
	methods: {
		loadProduct() {
			this.product = this.dataBase.getters.getProduct;
		},
		slideLeft() {
			this.$refs.productSwiper.swiper.slidePrev();
		},
		slideRight() {
			this.$refs.productSwiper.swiper.slideNext();
		},
		checkBarPos(height) {
			let num;
			if(typeof height === 'number') num = height;
			if(typeof height !== 'number') num = +document.querySelector('.product__ingredients').clientHeight * +document.querySelectorAll('.product__ingredient').length / 25;
			this.$refs.scrollBar.$el.scrollTop === 0 ? (this.scrollBarPos.start = true, this.scrollBarPos.end = false) : this.scrollBarPos.start = false;
			this.$refs.scrollBar.$el.scrollTop > num ? (this.scrollBarPos.end = true, this.scrollBarPos.start = false) : this.scrollBarPos.end = false;
		},
		barScroled(direction) {
			const length = document.querySelectorAll('.product__ingredient').length, 
						height = document.querySelector('.product__ingredients').clientHeight,
						pos = height * length / 25;
			direction === 'bottom' ? this.$refs.scrollBar.$el.scrollTop += pos : this.$refs.scrollBar.$el.scrollTop -= pos;
			this.checkBarPos(height - pos);
		},
		openProduct(obj) {
			this.$store.commit('setProductFrom', obj);
			this.cacheProduct = this.product;
			this.product = this.dataBase.getters.getProduct;
			
		},
		stepBack() {
			if(!this.cacheProduct) this.$router.back();
			if(this.cacheProduct) {
				this.product = this.cacheProduct;
				this.cacheProduct = null;
			}
		}
	},
}
</script>

<style lang="scss">

.full-window{
	min-height: 21em;
	max-height: 21em
}

.not-full-window{
	min-height: 16em;
	max-height: 16em
}

</style>


