<template>
	
	<div class="language__wrapper">
		<div class="language">
			<p class="language__current">{{currentLanguage}}</p>
			<ul class="language__list">
				<li class="language__list__item" @click='changeLanguage(item)' v-show='item !== currentLanguage' v-for='(item, index) in languages' :key="index">{{item}}</li>
			</ul>
		</div>
	</div>

</template>

<script>
export default {
	name: 'language',
	data() {
		return {
			currentLanguage: 'Ua',
			languages: [
				'Ua',
				'En',
				'Ru'
			]
		}
	},
	methods: {
		changeLanguage(lang) {
			this.currentLanguage = lang.charAt(0).toUpperCase() + lang.charAt(1);
			this.$store.commit('changeLang', lang.toLowerCase());
		}
	},
}
</script>

