import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
		requestUrl: '/data/data.json',
		allProducts: [],
		activeCategory: '',
		currentTypeProducts: '',
		openedProductId: null,
		pageLang: 'ua'
  },
  mutations: {
		loadData(state) {
			fetch( state.requestUrl, {method: 'GET'} )
			.then( response => {
				if(response.status === 200) return response.json();
			})
			.then( response => {
				state.allProducts = response;
			})
		},
		setCurrentCategory(state, category) {
			state.activeCategory = category;
			state.currentTypeProducts = state.allProducts[category];
		},
		setProduct(state, productId) {
			state.openedProductId = productId;
		},
		changeLang(state, lang) {
			state.pageLang = lang;
		},
		setProductFrom(state, obj) {
			state.activeCategory = obj.category;
			state.currentTypeProducts = state.allProducts[obj.category];
			state.openedProductId = obj.id;
		}
  },
  actions: {
	},
	getters: {
		getRequestUrl(state) {
			return state.requestUrl;
		},
		getAllProducts(state) {
			return state.allProducts;
		},
		getActiveProduct(state) {
			return state.currentTypeProducts;
		},
		getActiveCategory(state) {
			return state.activeCategory;
		},
		getProduct(state) {
			const productsMas = state.currentTypeProducts ? Array.from(state.currentTypeProducts) : [];
			// console.log(productsMas)
			return productsMas.find(element => element.id === state.openedProductId);
		}
	}
})
