<template>
	<main class="content bogel-menu">

			<div class="page-title">
				<img src="../assets/images/bogel-text-logo.svg" alt="text logo">
				<h1> {{$route.name.toLowerCase() === 'sales' ? title.name[$route.name][lang] : title.menu[lang] + ' ' + title.name[$route.name][lang]}}</h1>
			</div>

			<div class="page-content">
				
				<div class="page-content__slider-wrapper">
					
					<swiper class="page-content__slider" :options="swiperOption" ref="viewSwiper">
						<swiper-slide class="page-content__slide" v-for='(slide, index) in productsData' :key=index>

							
							<article class="slide" :class='{"is-sales" : category === "sales"}'>

								<div class="slide__view">
									
									<div class="slide__view__imgs">
										<img :src='slide["cat-img"]' :alt="slide.title[lang]" class="slide__img__cat">
										<img :src='slide["prod-img"]' :alt="slide.title[lang]" class="slide__img__view">
									</div>

								</div>

								<div class="slide__descr">
									
									<div class="label slide__price">
										<p>{{slide.price + ' &#8372;'}}</p>
									</div>
									
									<div class="slide__title">
										<h2>{{slide.title[lang]}}</h2>
									</div>

									<button @click='openProduct(slide.id)' class="slide__ingredients" to='/product'>
										<p data-key="ingredient">{{staticText.ingredients[lang]}}</p>
										<span></span>
									</button>
									
								</div>
								
							</article>

						</swiper-slide>
					</swiper>

					<!-- TODO: change logic of show or hide buttons -->
					<div :class='{"is-hidden" : productsData.length < 5}' class="page-content__nav">
						<button @click='slideLeft' class="page-content__arrow">
							<span></span>
						</button>
						<button @click='slideRight' class="page-content__arrow">
							<span></span>
						</button>
					</div>

				</div>

			</div>
	</main>
</template>


<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue';
import 'swiper/dist/css/swiper.css'

import { swiper, swiperSlide } from 'vue-awesome-swiper'

export default {
	name: 'view-page',
	components: {
		swiper,
    swiperSlide
	},
	props: ['base', 'lang'],
	data() {
		return {
			dataBase: this.base,
			staticText: {
				ingredients: {
					'ru': 'Ингредиенты',
					'ua': 'Інгредієнти',
					'en': 'Ingredients',
				},
			},
			title: {
				menu: {
					'ru': 'МЕНЮ',
					'ua': 'МЕНЮ',
					'en': 'Menu'
				},
				name: {
					'bogels': {
						'ru': 'BÖGEL',
						'ua': 'BÖGEL',
						'en': 'BÖGEL'
					},
					'desserts': {
						'ru': 'десерты',
						'ua': 'десерти',
						'en': 'dessert'
					},
					'drinks': {
						'ru': 'НАПИТКИ',
						'ua': 'НАПИТКИ',
						'en': 'drinks'
					},
					'sales': {
						'ru': 'Акции и предложения',
						'ua': 'Акції та пропозиції',
						'en': 'Sales and proposals'
					}
				}
			},
			swiperOption: {
				slidesPerView: 4.5,
				spaceBetween: 0,
				slidesPerGroup: 1,
				slidesOffsetBefore: 100,
				slidesOffsetAfter: 100,
			},
			swiper: null,
		}
	},

	created() {
		
	},
	mounted() {
		this.swiper = this.$refs.viewSwiper.swiper;
		this.checkForDragAvaliable();
		this.checkForCategory();
	},
	methods: {
		slideLeft() {
			if(this.productsData.length > 4) this.$refs.viewSwiper.swiper.slidePrev();
		},
		slideRight() {
			if(this.productsData.length > 4) this.$refs.viewSwiper.swiper.slideNext();
		},
		openProduct(id) {
			this.$store.commit('setProduct', id);
			this.$router.push('/product');
		},
		swiperSlidePosition() {
			if(this.productsData && this.productsData.length <= 4) this.swiper.slideTo(0, 0, false);
		},
		checkForCategory() {
			if(this.category === 'sales') {
				this.swiper.params.slidesPerView = 3.5;
				this.swiper.params.slidesOffsetBefore = 0;
				this.swiper.params.centeredSlides = true;
				this.swiper.params.slidesOffsetAfter = 0;
				this.swiper.params.spaceBetween = 10;
				this.swiper.update();
			} else {
				this.swiper.params.slidesPerView = 4.5;
				this.swiper.params.slidesOffsetBefore = 100;
				this.swiper.params.centeredSlides = false;
				this.swiper.params.slidesOffsetAfter = 100;
				this.swiper.params.spaceBetween = 0;
				this.swiper.update();
			}
		},
		checkForDragAvaliable() {
			if(this.productsData.length < 5) this.swiper.allowTouchMove = false;
			if(this.productsData.length > 4) this.swiper.allowTouchMove = true;
		}
	},
	computed: {
		category() {
			return this.dataBase.getters.getActiveCategory;
		},
		productsData() {
			return this.dataBase.getters.getActiveProduct;
		},
	},
	watch: {
		$route(to, from) {
			this.swiperSlidePosition();
			this.checkForDragAvaliable();
			this.checkForCategory();
		},
	}
}
</script>


